import { Container as ContainerB, Row } from 'react-bootstrap';
import styled from 'styled-components/macro';

const Container = styled(ContainerB)`
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
`;

export const Page = ({
  id,
  children,
  className,
}: {
  id: string;
  children: any;
  className?: string;
}) => {
  return (
    <Container id={id} className={className}>
      <Row>{children}</Row>
    </Container>
  );
};
